.wishes-section {
	position: relative;
	padding: 10rem 0 5rem;
	@include max-md {
		padding: 5rem 0 3rem;
		.row {
			gap: 1.5rem;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(10%, -30%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
			transform: translate(10%, -50%) scaleX(-1);
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		height: 90%;
		transform: translateX(-50%);
		@include max-md {
			transform: translateX(-15%);
		}
	}

	.splide {
		border-radius: 5px;
		background-color: var(--color-primary);
		box-shadow: 0 1rem 3rem rgba($color: #1e1f1e, $alpha: 0.175);
		.splide__arrows {
			.splide__arrow {
				top: 115%;
				background-color: var(--color-secondary);
				border-radius: 5px;
				height: 2.625em;
				width: 2.7em;
				path {
					fill: #fff;
				}
				&--prev {
					right: 3em;
					left: unset;
					@include max-md {
						right: 50%;
					}
				}
				&--next {
					right: 0;
					@include max-md {
						right: calc(50% - 3rem);
					}
				}
			}
		}
	}

	.wishes-preview {
		position: relative;
		color: var(--color-dark);
		.card {
			border: none;
			position: relative;
			z-index: 2;
			background-color: transparent;
			border-radius: 0;
			.card-body {
				overflow: hidden;
				.wishes {
					text-align: left;
					height: 94.5px;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					.text-muted {
						color: rgba($color: #1e1f1e, $alpha: 0.8) !important;
					}
					.wish {
						margin-bottom: 1.5rem;
						display: flex;
						flex-direction: column;
						justify-content: space-between;
						height: 100%;
						.text-muted {
							color: var(--color-dark) !important;
						}
						h6 {
							font-weight: 700;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
