.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 220px;
		transform: translate(-30%, 15%);
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 220px;
		transform: translate(30%, -15%);
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::before {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba($color: #000000, $alpha: 0.5);
			content: "";
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.thank {
		position: relative;
		text-align: center;
		padding: 8rem 2rem;
		z-index: 3;
		background-color: var(--color-primary);
		mask-image: url(../images/crystal-mask.png);
		mask-position: center;
		mask-size: 100% 100%;
		mask-repeat: no-repeat;
		@include max-md {
			padding: 5rem 1rem;
		}
		&::before {
			position: absolute;
			content: "";
			background-color: rgba($color: #000000, $alpha: 0.05);
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
		}
		* {
			position: relative;
		}
	}
	.crystal-border {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: calc(100% + 15px);
		height: calc(100% + 15px);
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	small {
		position: relative;
	}
}
