.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.container {
		z-index: 5;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		height: 220px;
		transform: translate(-50%, 35%);
		@include max-lg {
			height: 200px;
		}
		@include max-md {
			height: 180px;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		z-index: 3;
		border-radius: 10px;
		margin-top: -10rem;
		@include max-md {
			padding: 2rem 1.25rem;
		}
	}
	.image-story-wrap {
		position: relative;
		height: 560px;
		width: 100%;
		@include max-md {
			height: 480px;
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
