.logo-viding {
	height: 2.4rem;
	@include max-md {
		height: 2.25rem;
	}
}

.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.border-orn {
		position: absolute;
		left: 50%;
		top: 0;
		width: 52%;
		transform: translateX(-50%);
		@include max-lg {
			width: 80%;
		}
		@include max-md {
			width: 110%;
		}
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 120px;
		@include max-md {
			width: 80px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 120px;
		@include max-lg {
			width: 100px;
		}
		@include max-md {
			width: 80px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 220px;
		transform: translateX(-40%);
		@include max-md {
			width: 160px;
		}
	}
	.orn-4 {
		position: absolute;
		top: 0;
		right: 0;
		width: 220px;
		transform: translateX(40%);
		@include max-md {
			width: 160px;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 4rem 0 0;
		@include max-lg {
			padding: 8rem 0 0;
		}
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 1.5rem 0 3rem;
			position: relative;
			z-index: 3;
			.image-wrapper {
				position: relative;
				height: 70%;
				aspect-ratio: 4/6;
				transition: all 0.25s ease-in-out;
				&:hover {
					transform: scale(1.05);
				}
				.background-header {
					width: 100%;
					height: 100%;
					object-fit: cover;
					-webkit-mask-image: url(../images/crystal-mask.png);
					mask-image: url(../images/crystal-mask.png);
					-webkit-mask-size: 100% 100%;
					mask-size: 100% 100%;
					-webkit-mask-position: center;
					mask-position: center;
					-webkit-mask-repeat: no-repeat;
					mask-repeat: no-repeat;
				}
				.image-crystal-border {
					width: calc(100% + 15px);
					height: calc(100% + 15px);
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}
			}
			.header-title {
				position: relative;
				margin-top: 2.5rem;
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.35);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		.header {
			justify-content: space-between;
			.header-title {
				margin-bottom: 0;
				padding: 3rem;
				@include max-md {
					padding: 3rem 0.75rem;
				}
			}
		}
	}
}
