.rsvp-section {
	position: relative;
	padding: 3rem 0;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		height: 240px;
		transform: translate(-50%, 50%);
		@include max-lg {
			height: 220px;
		}
		@include max-md {
			height: 160px;
		}
	}
	.image-wrap {
		position: relative;
		height: 520px;
		border-radius: 10px;
		position: absolute;
		top: -8rem;
		left: 0;
		@include max-md {
			position: relative;
			top: 0;
			height: 380px;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 10px;
		}
	}
	.rsvp-wrap {
		padding: 5rem 2rem;
		position: relative;
		z-index: 3;
		background-color: var(--color-primary);
		border-radius: 10px;
		@include max-md {
			padding: 3rem 1.5rem;
		}
	}
	.border-ornament {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 65%);
		width: 50%;
		height: auto;
		@include max-lg {
			width: 70%;
		}
		@include max-md {
			transform: translate(-50%, 90%);
		}
	}
}
