.venue-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 8rem;
		left: 0;
		width: 240px;
		transform: translate(-30%, -30%);
		@include max-md {
			width: 200px;
			top: 5rem;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		bottom: 8rem;
		width: 240px;
		transform: scaleX(-1) translate(-30%, 30%);
		@include max-md {
			width: 180px;
			bottom: 5rem;
		}
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			.crystal-border {
				position: absolute;
				top: 0;
				left: 50%;
				transform: translateX(-50%);
				height: 100%;
				width: auto;
				@include max-md {
					top: 50%;
					transform: translate(-50%, -50%);
					width: 100%;
					height: auto;
				}
			}
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: 10px;
				mask-image: url(../images/crystal-mask-lg.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					height: 100%;
					width: 100%;
					background-color: rgba($color: #000000, $alpha: 0.05);
					content: "";
				}
				.card-body {
					position: relative;
					padding: 10rem 2rem;
					@include max-md {
						padding: 8rem 1rem;
					}
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-dark);
		color: var(--color-dark);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary);
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
	.maps-embed {
		width: 100%;
		position: relative;
		height: auto;
		aspect-ratio: 16/9;
	}
}
