.couple-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 220px;
		transform: translate(-30%, 15%);
		@include max-md {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 220px;
		transform: translate(30%, -15%);
		@include max-md {
			width: 160px;
		}
	}
	.orn-3 {
		position: absolute;
		left: 0;
		top: 0;
		height: 105%;
	}
	.orn-4 {
		position: absolute;
		right: 0;
		top: 0;
		height: 105%;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
		}
		margin: 1rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.couple {
		position: relative;
		.image-wrap {
			// width: 100%;
			margin: auto;
			position: relative;
			margin-bottom: 1.25rem;
			aspect-ratio: 4/6;
			height: 320px;
			transition: all 0.25s ease-in-out;
			@include max-md {
				width: 80%;
				height: auto;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				mask-image: url(../images/crystal-mask.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
			}
			.image-crystal-border {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: calc(100% + 15px);
				height: calc(100% + 15px);
				&.scaleX-reverse {
					transform: scaleX(-1);
				}
			}
		}
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
