.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		height: 240px;
		transform: translate(-50%, 50%);
		@include max-lg {
			height: 220px;
		}
		@include max-md {
			height: 180px;
		}
	}
	.border-ornament {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 70%);
		width: 65%;
		max-height: 100%;
		height: auto;
		@include max-lg {
			// transform: translate(-50%, 80%);
			width: 75%;
		}
	}
	.appology-wrap {
		position: relative;
		border-radius: 10px;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
